label {
    color: $gray;
    display: block;
    font-size: 0.8em;
    font-weight: 600;
    letter-spacing: 0.25em;
    margin: 0 0 1em 0;
    text-transform: uppercase;
}

::-webkit-input-placeholder {
    color: rgba(244, 244, 255, 0.2) !important;
    opacity: 1.0;
}

:-moz-placeholder {
    color: rgba(244, 244, 255, 0.2) !important;
    opacity: 1.0;
}

::-moz-placeholder {
    color: rgba(244, 244, 255, 0.2) !important;
    opacity: 1.0;
}

:-ms-input-placeholder {
    color: rgba(244, 244, 255, 0.2) !important;
    opacity: 1.0;
}


/* Contact Method */

.contact-method {
    margin: 0 0 2em 0;
    padding-left: 3.25em;
    position: relative;
}

    .contact-method .icon {
        left: 0;
        position: absolute;
        top: 0;
    }

    .contact-method h3 {
        margin: 0 0 0.5em 0;
    }

    /* Contact */

#contact {
    border-bottom: solid 1px rgba(212, 212, 255, 0.1);
    overflow-x: hidden;
}

#contact > .inner {
    display: -moz-flex;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    padding: 0 !important;
}

#contact > .inner > :nth-child(2n - 1) {
    padding: 4em 3em 2em 0 ;
    border-right: solid 1px rgba(212, 212, 255, 0.1);
    width: 60%;
}

#contact > .inner > :nth-child(2n) {
    padding-left: 3em;
    width: 40%;
}

#contact > .inner > .split {
    padding: 0;
}

#contact > .inner > .split > * {
    padding: 3em 0 1em 3em ;
    position: relative;
}

#contact > .inner > .split > *:before {
    border-top: solid 1px rgba(212, 212, 255, 0.1);
    content: '';
    display: block;
    margin-left: -3em;
    position: absolute;
    top: 0;
    width: calc(100vw + 3em);
}

#contact > .inner > .split > :first-child:before {
    display: none;
}

@media screen and (max-width: 980px) {

#contact > .inner {
    display: block;
}

#contact > .inner > :nth-child(2n - 1) {
    padding: 4em 0 2em 0 ;
    border-right: 0;
    width: 100%;
}

#contact > .inner > :nth-child(2n) {
    padding-left: 0;
    width: 100%;
}

#contact > .inner > .split > * {
    padding: 3em 0 1em 0 ;
}

#contact > .inner > .split > :first-child:before {
    display: block;
}

}

@media screen and (max-width: 736px) {

    #contact > .inner > :nth-child(2n - 1) {
        padding: 3em 0 1em 0 ;
    }

}
